import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CheckProgress } from '@app/shared/models/check-progress';

@Component({
  selector: 'app-other-board',
  templateUrl: './other-board.component.html',
  styleUrls: ['./other-board.component.css']
})
export class OtherBoardComponent implements OnInit {

  public questionBank: CheckProgress;

  constructor(private _activeRoute: ActivatedRoute, private _router: Router) {
    this._router.routeReuseStrategy.shouldReuseRoute = () => false;
   }

  ngOnInit() {
    this.questionBank = this._activeRoute.snapshot.data["questionBank"];
 

  }

  goToList(data) {
    this._router.navigate([data.moduleType], { relativeTo: this._activeRoute });

  }

}
