import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TableListService } from '@app/core/http/serviceIndex';
import { Prediction, PredictionList } from '@app/shared/models/prediction';
import { TableFilter } from '@app/shared/models/table-filter';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-other-list',
  templateUrl: './other-list.component.html',
  styleUrls: ['./other-list.component.css']
})
export class OtherListComponent implements OnInit {

  public currentModule: string;

  public tableFilter = <TableFilter>{};
  public isLoading: boolean = false;
  public otherList: Prediction[];
  private subscription: Subscription;
  private type: any;
  private isAsc:boolean=false;

  constructor(private _activeRoute: ActivatedRoute, private _tableService: TableListService) { }

  ngOnInit() {
    this.currentModule = this._activeRoute.snapshot.params['moduleName'];
    this.type = this._activeRoute.parent.snapshot.params['type'];
    this._activeRoute.queryParams.subscribe(
      (params) => {
        if (params.page) {
          this.tableFilter.page = params.page;
          this.tableFilter.limit = params.limit;
        } else if (params.search) {
          this.tableFilter.search = params.search;
        } else {
          this.tableFilter.page = 1;
          this.tableFilter.limit = 10;
          this.tableFilter.sortDirection = 'asc';
          this.tableFilter.sortColumn = 'name';
          this.tableFilter.search = '';
        }
        this.gePredictionsList();
      }
    )
  }

  gePredictionsList() {
    this.isLoading = true;
    this.subscription = this._tableService.getOtherList(this.tableFilter, this.currentModule, this.type)
      .subscribe((res: PredictionList) => {
        this.otherList = res.questions;
        this.tableFilter = res.filter;
        this.isLoading = false;
      });
  }

  fitter(sortColumn) {
    this.isAsc = !this.isAsc ? this.isAsc = true : this.isAsc = false;
    this.tableFilter.sortDirection = this.isAsc ? 'asc' : 'desc';
    this.tableFilter.sortColumn = sortColumn;
    this.gePredictionsList();
  }


  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    if (this.subscription) this.subscription.unsubscribe();
  }
}
