import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { OtherQuestionResolver } from '@app/modules/manage/reslovers/otherQuestion.reslover';

import { OtherBoardComponent } from '@app/modules/other-question-module/other-board/other-board.component';
import { OtherListComponent } from '@app/modules/other-question-module/other-list/other-list.component';
import { OtherQuestionComponent } from '@app/modules/other-question-module/other-question/other-question.component';


const routes: Routes = [
  {
    path: ':type',
    component: OtherQuestionComponent,
    children: [
      {
        path: '',
        component: OtherBoardComponent,
        resolve: {
          questionBank: OtherQuestionResolver
        },
      },
      {
        path: ':moduleName',
        component: OtherListComponent,
      }
    ]
  },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
  providers: [
    OtherQuestionResolver
  ]
})
export class OtherRoutingModule { }